<!--@Auther: VanZhi -->
<template>
  <van-datetime-picker
    v-model="currentDate"
    type="datetime"
    :min-date="minDate"
    :max-date="maxDate"
  />
</template>

<script>
  import {DatetimePicker} from 'vant';

  export default {
    name: "DataAnalysis",
    components: {[DatetimePicker.name]: DatetimePicker},
    data() {
      return {
        minHour: 10,
        maxHour: 20,
        minDate: new Date(),
        maxDate: new Date(2019, 10, 1),
        currentDate: new Date()
      };
    }
  }
</script>

<style scoped>

</style>



