<template>
  <div>
    <van-nav-bar
      title="标题"
      left-text="返回"
      right-text="按钮"
      left-arrow
    />
    <van-cell-group>
      <van-field
        v-model="title"
        label="标题"
        icon="close"
        placeholder="请输入标题"
        @click-icon="title = ''"
      />
    </van-cell-group>

    <van-cell-group>
      <van-field
        v-model="money"
        label="金额"
        icon="close"
        placeholder="请输入金额"
        @click-icon="tempMoney = '';money = ''"
        @click="selectMoney($event)"
      />
    </van-cell-group>

    <van-cell-group>
      <van-field
        v-model="currentDateFormat"
        label="时间"
        icon="close"
        placeholder="点击选择时间"
        @click-icon="date = ''"
        @click="selectDate($event)"
      />
    </van-cell-group>

    <van-cell-group style="margin: 15px">
      <van-button style="border-radius: 7px;" type="primary" size="large" @click="onSubmit">确定</van-button>
      <!--<van-button style="border-radius: 7px;margin-top: 20px" type="primary" size="large">按钮</van-button>-->
    </van-cell-group>


    <!--<van-tabbar v-model="active">-->
      <!--<van-tabbar-item icon="shop">标签</van-tabbar-item>-->
      <!--<van-tabbar-item icon="chat" dot>标签</van-tabbar-item>-->
      <!--<van-tabbar-item icon="records" info="5">标签</van-tabbar-item>-->
      <!--<van-tabbar-item icon="gold-coin" info="20">标签</van-tabbar-item>-->
    <!--</van-tabbar>-->

    <van-popup v-model="show" position="bottom" @confirm="">
      <van-datetime-picker
        v-model="currentDate"
        type="datetime"
        :min-date="minDate"
        :max-date="maxDate"
        loading="true"
      />
    </van-popup>

    <van-number-keyboard
      :show="numberShow"
      theme="custom"
      extra-key="."
      close-button-text="完成"
      @blur="numberShow = false"
      @input="onInput"
      @delete="onDelete"
      v-model="money"
    />

  </div>
</template>

<script>
  import {Field, CellGroup, Popup, DatetimePicker, NavBar, NumberKeyboard, Button, Tabbar, TabbarItem, Toast } from 'vant';
  import {formatDate} from './dateFormat';
  import axios from "axios"
  import Qs from 'qs'

  export default {
    name: "MoneyAccounts",
    components: {
      [Field.name]: Field,
      [CellGroup.name]: CellGroup,
      [Popup.name]: Popup,
      [DatetimePicker.name]: DatetimePicker,
      [NavBar.name]: NavBar,
      [NumberKeyboard.name]: NumberKeyboard,
      [Button.name]: Button,
      [Tabbar.name]: Tabbar,
      [TabbarItem.name]: TabbarItem,
      Toast
    },
    data() {
      return {
        title: '',
        money: '',
        tempMoney: '',
        date: '',
        remark: '',
        show: false,
        numberShow: false,
        minHour: 10,
        maxHour: 20,
        minDate: new Date(),
        maxDate: new Date(2019, 10, 1),
        currentDate: new Date(),
        active: 1
      }
    },
    methods: {
      selectDate(v) {
        this.show = !this.show;
        let that = v.target;
        that.blur();
      },
      selectMoney(v) {
        this.numberShow = !this.numberShow;
        let that = v.target;
        that.blur();
      },
      onInput(value) {
        // Toast(value);
        this.tempMoney = this.tempMoney + value.toString();
        this.money = this.tempMoney
      },
      onDelete() {
        this.tempMoney = this.tempMoney.substring(0, this.tempMoney.length - 1);
        this.money = this.tempMoney
      },
      onSubmit() {
        axios({
          method: "post",
          url: "/api/stats/api/money",
          // headers: {
          //   "Content-type": "application/x-www-form-urlencoded"
          // },
          data: {title: this.title, money: this.money, date: this.currentDateFormat}
        })
        .then(function (response) {
          Toast.success('操作成功');
          console.log(response);
        })
        .catch(function (error) {
          console.log(error);
        });
      }
    },
    computed: {
      currentDateFormat: function () {
        return formatDate(this.currentDate, "yyyy-MM-dd")
      }
    }
  }
</script>

<style scoped>

</style>



